.link-arrow {
  display: block;
  font-weight: 500;
  font-size: rem(15px);
  line-height: rem(20px);
  text-transform: uppercase;
  text-decoration: none;
  color: $blue-default;

  transition: all 0.4s ease;

  & span {
    padding-right: 6px;
  }

  &:hover,
  &:focus {
    color: $blue-accent;
  }

  &--small {
    font-size: rem(13px);
  }

  &--grey {
    color: $grey-default;
  }
}
